.navbar {
    padding-top: 1.5rem;
}

.input-group {
    width: 40rem;
}

.jupyter-logo,
.markdown-logo {
    height: 2rem;
    width: auto;
    margin-left: .5rem;
    margin-right: .5rem;
}

.right-arrow {
    margin-left: .5rem;
    margin-right: .5rem;
    height: 1.5rem;
    width: auto;
}

.video-logo {
    height: 2.7rem;
    width: auto;
}


.hyperparameter-form {
    input {
        background-color: black;
        color: white;
        border-color: gray;
        display: inline-block;
        width: 15rem;
    }

    .hyperparameter p {
        &:hover {
            cursor: pointer;
        }
    }
}

.content-box {
    overflow: hidden !important;

    .original-markdown-box {
        overflow-x: scroll !important;

        .file-content-box {
            width: 100%;
            height: 50%;
            flex-wrap: wrap !important;
            overflow: scroll !important;
        }
    }

    .decode-box {
        .list-group {
            background: none !important;
            border: none;

            .list-group-item {
                background: none !important;
                border: none;
            }
        }
    }

}

.modal {
    .list-group {
        background: none !important;

        .list-group-item {
            border: none;
            background: none !important;
        }
    }
}

#goTopBtn {
    position: fixed;
    bottom: 1rem;
    right: 1rem;
}

#refreshBtn {
    position: fixed;
    bottom: 4rem;
    right: 1rem;
}

// 衬底，将内容区域都衬底为黑色
.content-box {
    background-color: #000;
    height: 100rem;
    width: 100%;
}

#tutorial-box {
    .tutorial-container {}


    .content-area {

        .graph-box {
            .graph {
                height: 20rem;
                width: auto;
            }
        }

        .voice-box {
            inline-size: 100% !important;
            overflow-wrap: break-word !important;

            .voice-second {
                display: none; //不显示语音输出的秒数的数字
                margin-right: 0.6px;
            }
        }

        .title-letter {
            margin-right: 0.6px;
        }

        .desc-letter {
            margin-right: 0.6px;
        }

        .code-box {
            inline-size: 100% !important;
            overflow-wrap: break-word !important;

            .code-letter {
                inline-size: 100% !important;
                overflow-wrap: break-word !important;
                margin-right: .7px;
            }
        }

        .output-box {
            inline-size: 100% !important;
            overflow-wrap: break-word !important;

            .output-letter {
                margin-right: 0.6px;
            }

        }
    }
}

#file-input-label {
    &:hover {
        background-color: rgb(8, 116, 67) !important;
        cursor: pointer;
    }
}